import './vd.css'
import utatapreview from '../../img/utatapreview.mp4'
import uposter from '../../img/0400.png'
function videopage(){
    const vp = document.createElement('video')
    vp.classList.add('vp')
    vp.setAttribute('poster', uposter )
    const vs =  document.createElement('source')
    vs.setAttribute('src', utatapreview)
    vp.controls = true
    vp.appendChild(vs)
    return vp
    
}
function vd(){
    const vdc = document.createElement('div')
    vdc.classList.add('vd')
    const cb = document.createElement('button')
    cb.classList.add('cancelbuttonwhite')
    cb.addEventListener('click', e=>{
        let cardz = document.getElementsByClassName('cards')[0]
        cardz.classList.remove('active')
        let vida = document.getElementsByClassName('vd')[0]
        vida.classList.remove('active')
        document.getElementById('catalog').classList.remove('active')
        let clip = document.getElementsByClassName('videos')[0]
        clip.classList.remove('active')
        let vb = document.getElementsByClassName('watchbutton')[0]
        vb.style.top = '80%'
        let inactiv = document.getElementsByClassName('videosbase')[1]
        inactiv.classList.remove('inactive')
        let pv = document.getElementsByClassName('vp')[0]
        pv.pause()

    })
    let vpc = videopage();
    vdc.appendChild(vpc)
    vdc.appendChild(cb)
    return vdc
}

export {vd}