import { lowerCase } from 'lodash'
import './footer.css'
import tw from '../../img/twitterb.svg'
import ig from '../../img/instagram.svg'
import fb from '../../img/facebook.svg'
function getlist(){
    const list =  document.createElement('dl')

    return list
}
function getp(){
    const para = document.createElement('p')

    return para
}
function caption(){
    const capt = document.createElement('caption')

    return capt
}
function getimg(){
    const img =  document.createElement('img')

    return img
}
function contacts() {
    const contacts = document.createElement('dt')
    contacts.classList.add('contacts')
    contacts.setAttribute('id', 'contactus')
    let l1 = getlist()
    let l2 = getlist()
    let cp = caption()
    l1.innerHTML = '<bdi><b>Email</b></bdi>: jasenaproductions@gmail.com'
    l2.innerHTML = '<bdi><b>Phonenumber</b></bdi>: +254'
    cp.innerHTML = 'Get in touch'
    contacts.appendChild(cp)
    contacts.appendChild(l1)
    contacts.appendChild(l2)
    return contacts
}
function services(){
    const services = document.createElement('dt')
    services.classList.add('services')
    let l1 = getlist()
    let l2 = getlist()
    let l3 = getlist()
    let l4 = getlist()
    let l5 = getlist()
    let l6 = getlist()
    let p1 =  caption()

    l1.innerHTML = 'Location Scouting'
    l2.innerHTML = 'Talent Scouting'
    l3.innerHTML = 'Script Development'
    l4.innerHTML = 'Production'
    l5.innerHTML = 'Post Production'
    l6.innerHTML = 'Distribution'
    p1.innerHTML = 'Our Services'

    services.appendChild(p1)
    services.appendChild(l1)
    services.appendChild(l2)
    services.appendChild(l3)
    services.appendChild(l4)
    services.appendChild(l5)
    services.appendChild(l6)

    return services
}
function socialmedia(){
    const community = document.createElement('dt')
    community.classList.add('community')

    let l1 = getimg()
    l1.setAttribute('src',tw)
    let l2 = getimg()
    l2.setAttribute('src',ig)
    let l3 = getimg()
    l3.setAttribute('src',fb)
    let p1 =  caption()

    p1.innerHTML = 'Follow Us on Social Media'
    var imgholster = document.createElement('div')
    imgholster.classList.add('imgholster')

    imgholster.appendChild(l1)
    imgholster.appendChild(l2)
    imgholster.appendChild(l3)

    community.appendChild(p1)
    community.appendChild(imgholster)
    return community
}
function footercontainer(){
    const ftc = document.createElement('dt')
    ftc.classList.add('ftc')

    let cnt = contacts()
    let srv = services()
    let cm = socialmedia()

    ftc.appendChild(srv)
    ftc.appendChild(cnt)
    ftc.appendChild(cm)

    return ftc
}
function footer() {
    const footer = document.createElement('footer');
    footer.classList.add('footer');

    let ft = footercontainer()
    footer.appendChild(ft);

    footer.addEventListener('click',e=>{
        let menu_ls = document.getElementsByClassName('menulist')[0]
        if(menu_ls.classList.contains('active')){
            menu_ls.classList.remove('active')
        }
    
    })
return footer
}

export { footer }