import './card.css'
import ucast from '../../img/utatast2.jpg'
function videos(){
    const vid  =  document.createElement('div');
    vid.classList.add('videos');

    return vid
}
function videocard(){
    const movie = document.createElement('div');
    movie.classList.add('videocard')

    return movie
}

function videosbase(){
    const vidb  =  document.createElement('div');
    vidb.classList.add('videosbase');
    let vc = videocard();
    let vd = videos();
    let utimg = document.createElement('img')
    utimg.setAttribute('src', ucast)
    vd.appendChild(utimg)

    vidb.appendChild(vc)
    vidb.appendChild(vd)
    const watchnowbutton = document.createElement('a');
    watchnowbutton.classList.add('watchbutton')
    watchnowbutton.innerHTML = "watch preview"
    watchnowbutton.addEventListener('mouseover',e=>{
        vc.classList.add('active')
        vd.classList.add('hover')
    })
    watchnowbutton.addEventListener('mouseout',e=>{
        vc.classList.remove('active')
        vd.classList.remove('hover')
    })
    vidb.appendChild(watchnowbutton)
    return vidb
}


function card(){
    const cards = document.createElement('div');
    cards.classList.add('cards');
    let vb = videosbase();
    vb.firstChild.setAttribute('id', 'v1');
    vb.lastChild.addEventListener('click',e=>{
        let db = document.getElementsByClassName('videosbase')[0]
        db.nextElementSibling.classList.add('inactive')
        vb.lastChild.style.top = '100%'
        document.getElementById('catalog').classList.add('active')
        let cardz = document.getElementsByClassName('cards')[0]
        cardz.classList.add('active')
        let videocardz = document.getElementsByClassName('vd')[0]
        videocardz.classList.add('active')
        let clip = document.getElementsByClassName('videos')[0]
        clip.classList.add('active')

    })
    let vb2 = videosbase();
    vb2.firstChild.setAttribute('id', 'v2');

    cards.appendChild(vb)
    cards.appendChild(vb2)

    return cards
}

export {card}